import AbstractObservable from '../utils/pattern/abstract-observable';
import ApiService from '../api-service';

export default class MembersModule extends AbstractObservable {
	#members = [];
	// #attempts = [];
	
	#apiService = new ApiService();

	get get() {
		return this.#members;
	}

	// get getAttempts() {
	// 	return this.#attempts;
	// }

	init = async () => {
		try {
			this.#members = await this.#apiService.members;
			// this.#attempts = await this.#apiService.attempts;
		} catch(err) {
			console.error(err);
			this.#members = [];
			// this.#attempts = [];
		}

		this._notify();
	}
};
