import MemberView from '../view/member-view';
import TableContentView from '../view/table-content-view';
import { RenderPosition } from '../const';
import { render } from '../utils/render';

const TBODY_CLASS = '.table-page__tbody';

export default class tablePresenter {
	#viewportElement = null;
	#tbodyElement = null;

	#memberComponent = new MemberView();
	#tableContentComponent = new TableContentView();
	
	constructor(viewportElement) {
		this.#viewportElement = viewportElement;
	}

	init = () => {
		this.#renderTableContent();
		this.#renderMember();
	}

	#renderTableContent = () => {
		
		this.#tbodyElement = this.#tableContentComponent.element.querySelector(TBODY_CLASS);

		render(this.#viewportElement, this.#tableContentComponent, RenderPosition.BEFORE_END)
	}

	#renderMember = () => {
		for (let i = 0; i < 20; i++) {
			render(this.#tbodyElement, new MemberView(), RenderPosition.BEFORE_END);
		}
	}
}